.about-section-parent {
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-section-parent .about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  width: 87%;
}
.about-section-parent .about-section h1 {
  font-size: 30px;
  border-bottom: solid 2px rgba(60, 166, 166, 0.9333333333);
}
.about-section-parent .about-section p {
  padding: 0 20px;
  max-width: 800px;
  padding-bottom: 60px;
  margin-top: 40px;
}
.about-section-parent .about-section h2 {
  font-size: 20px;
  margin-bottom: 10px;
}
.about-section-parent .about-section .license {
  padding: 20px;
  border-radius: 9px;
  max-width: 300px;
  background-color: rgba(60, 166, 166, 0.9333333333);
}
.about-section-parent .about-section .license li {
  margin-top: 3px;
}
.about-section-parent .about-section .skills h2 {
  margin-top: 90px;
  text-align: center;
  font-size: 25px;
  margin-bottom: 30px;
}
.about-section-parent .about-section .skills .skill-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.about-section-parent .about-section .skills .skill-item .tec-img {
  width: 50px;
  margin: 0 20px;
}/*# sourceMappingURL=AboutMe.css.map */